.App{
    font-family: 'Montserrat', sans-serif;
}

.img{
    max-width: 100%;
    width: 100px;
}

.link{
    margin: 0;
    color: white;
    text-decoration: none;
}

.wrapp{
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
}



.TodoList_wrapp__13ojv {
    width: 80%;
}

.TodoList_img__3GJx2 {
    position: fixed;
    width: 30%;
    right: 20px;
    top: 85px;
    z-index: -1;
}

@media only screen and (max-width: 992px) {
    .TodoList_img__3GJx2 {
        display: none;
    }

    .TodoList_wrapp__13ojv {
        width: auto;
    }
}

.StartPage_h1__29U-U {
    text-align: center;
    font-size: 64px;
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    margin-top: 30px;
}

.StartPage_h1__29U-U span {
    text-transform: none;
}

.StartPage_section__1Ba8d {
    position: relative;
}

.StartPage_img__D4W2g {
    width: 90%;
    top: 75px;
    left: 185px;
    position: absolute;
    z-index: -1;
    transition: all .7s;
}

.StartPage_btnWrapper__3mx2Q {
    display: flex;
    width: 50%;
    margin: 0 auto;
    grid-gap: 20px;
    gap: 20px;
}

.StartPage_btn__3olVI {
    margin: 0 auto;
    display: block;
    background: #ffc926;
    border: none;
    border-radius: 15px;
    font-size: 32px;
    padding: 10px 30px;
    cursor: pointer;
    box-shadow: inset 0 0 0 0 #7063ff;
    transition: all 0.33s ease-in;

    &:hover {
        background: #7063ff;
        color: #fff;
    }
}

.StartPage_btn__3olVI:focus {
    outline: none;
    box-shadow: inset 0 0 0 3rem #7063ff;
    color: #fff;
}

.StartPage_link__18WbO {
    color: black;
    margin: 0;
    text-decoration: none;
}

.StartPage_link__18WbO:focus {
    outline: none;
}

